#botitle{
    grid-area: 1/1/2/2;
    text-align: left;
    margin:5px;
    font-weight: bold;
}

#bokauthor{
    grid-area: 2/1/3/2;
    text-align: left;
    margin:5px;
}
#stat{
    grid-area: 1/2/3/3;
    border:none;
    border-radius: 8px;
    padding:8px;
    color:white;
    font-weight: bold;
    width:150px;
}

#bkinlst{
    display: grid;
    grid-template-columns: auto auto; 
    margin: 20px;
    padding:10px;
    border-radius: 12px;
    -webkit-box-shadow: 0 0 20px 1px #b3aeae;
            box-shadow: 0 0 20px 1px #b3aeae;
    -webkit-animation-name: fadein;
            animation-name: fadein;
    -webkit-animation-duration: 500ms;
            animation-duration: 500ms;
    -webkit-animation-delay: 100ms;
            animation-delay: 100ms;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}


